.header--index::before {
  @media (min-width: $desktop-width) {
    background-image: url("../img/index-cat-background.jpg"), linear-gradient(to right, rgba(80, 155, 34, 0.85), rgba(70, 144, 24, 0.85));
    content: "";
    height: 400px;
    width: 50%;
    display: block;
    background-repeat: no-repeat, no-repeat;
    background-position: top right, top;
    position: absolute;
    right: 0;
  }
}

.header {
  position: relative;
}

.header__wrapper {
  display: flex;
  flex-direction: column;
  padding-top: 10px;

  @media (min-width: $tablet-width) {
    flex-direction: row;
    justify-content: space-between;
    padding-top: 0;
  }

  @media (min-width: $desktop-width) {
    padding-top: 53px;
  }
}

.header__inner {
  @media (min-width: $tablet-width) {
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 60px;
  }

  @media (min-width: $desktop-width) {
    margin-top: 53px;
    margin-bottom: 82px;
  }
}

.header__logo {
  display: flex;
  padding-bottom: 24px;

  @media (min-width: $tablet-width) {
    width: 174px;
  }

  @media (min-width: $desktop-width) {
    width: 202px;
  }
}

.header__toggle {
  position: absolute;
  padding: 0;
  right: 0;
  display: block;
  width: 54px;
  height: 47px;
  background-color: rgb(255, 255, 255);
  cursor: pointer;
  border: none;


  @media (min-width: $tablet-width) {
    display: none;
  }

  @media (min-width: $desktop-width) {
    display: none;
  }
}

.header__wrapper--closed .header__toggle::before {
  content: "";
  position: absolute;
  top: 13px;
  left: 16px;
  width: 24px;
  height: 2px;
  background-color: rgb(0, 0, 0);
  box-shadow: 0 8px 0 0 rgb(0, 0, 0), 0 16px 0 0 rgb(0, 0, 0);
}

.header__wrapper--closed .header__toggle:active::before {
  background-color: rgba(0, 0, 0, 0.3);
  box-shadow:
    0 8px 0 0 rgba(0, 0, 0, 0.3),
    0 16px 0 0 rgba(0, 0, 0, 0.3);
}

.header__wrapper--opened .header__toggle::before {
  transform: rotate(45deg);
  background-color: rgb(0, 0, 0);
  box-shadow: none;
  content: "";
  position: absolute;
  top: 23px;
  left: 16px;
  width: 24px;
  height: 2px;

  @media (min-width: $tablet-width) {
    display: none;
  }
}

.header__wrapper--opened .header__toggle:active::before {
  background-color: rgba(0, 0, 0, 0.3);
}

.header__wrapper--opened .header__toggle::after {
  transform: rotate(-45deg);
  background-color: rgb(0, 0, 0);
  box-shadow: none;
  content: "";
  position: absolute;
  top: 23px;
  left: 16px;
  width: 24px;
  height: 2px;
}

.header__wrapper--opened .header__toggle:active::after {
  background-color: rgba(0, 0, 0, 0.3);
}

@media (max-width: $mobile-width-only) {
  .header__wrapper--closed .site-list__item {
    display: none;
  }

  .header__wrapper--opened .site-list {
    position: absolute;
    left: -10px;
    background-color: $basic-white;
    width: 100vw;
  }
}

.header__wrapper--nojs {
  .header__toggle {
    display: none;
  }
}
