.main-intro__wrapper {
  display: grid;
  grid-template-rows: 99px 68px 135px 135px 42px;
  grid-template-columns: 1fr 280px 1fr;
}

.main-intro__wrapper::before {
  background-image: url("../img/index-cat-background.jpg"), linear-gradient(to right, rgba(80, 155, 34, 0.85), rgba(70, 144, 24, 0.85));
  content: "";
  height: 302px;
  display: block;
  background-repeat: no-repeat, no-repeat;
  background-position: top center, top;
  grid-row: 1/3;
  grid-column: 1/4;
}

.main-intro__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 36px;
  color: $basic-white;
  line-height: 36px;
  grid-row: 1;
  grid-column: 2/3;
  margin: 0;
  padding: 0;
  text-align: center;
  padding-top: 27px;

  @media (min-width: $tablet-width) {
    padding-top: 0;
  }
}

.main-intro__slogan {
  font-family: "Oswald", "Arial", sans-serif;
  color: $basic-white;
  text-transform: uppercase;
  grid-row: 2;
  grid-column: 2/3;
  margin: 0;
  padding: 0;
  text-align: center;
  padding-top: 25px;
}

.main-intro__image {
  display: block;
  grid-row: 3/5;
  grid-column: 2/3;
}

.main-intro__link {
  grid-row: 5/6;
  grid-column: 2/3;
}

@media (min-width: $tablet-width) {
  .main-intro__wrapper {
    grid-template-rows: 120px 81px 83px 187px;
    grid-template-columns: 1fr 709px 1fr;
  }

  .main-intro__wrapper::before {
    display: none;
  }

  .main-intro__title {
    color: $black;
    margin-left: 91px;
    line-height: 60px;
    font-size: 60px;
    font-weight: 400;
    text-align: left;
  }

  .main-intro__slogan {
    color: $black;
    margin-left: 91px;
    padding-top: 41px;
    line-height: 20px;
    font-size: 20px;
    text-align: left;
  }

  .main-intro__image {
    grid-row: 3/5;
  }

  .main-intro__link {
    grid-row: 3/4;
    margin-left: 91px;
    margin-top: 49px;
    width: 192px;
  }
}

@media (min-width: $desktop-width) {
  .main-intro__wrapper {
    display: block;
    margin-bottom: 82px;
    position: relative;
  }

  .main-intro__title {
    width: 414px;
    margin-bottom: 40px;
    margin-left: 80px;
    font-size: 60px;
    line-height: 60px;
    font-weight: 400;
  }

  .main-intro__slogan {
    margin-bottom: 52px;
  }

  .main-intro__image {
    bottom: 144px;
    position: absolute;
    right: 400px;
  }

  .main-intro__link {
    margin: 0;
    margin-left: 80px;
  }
}
